export default {
	data() {
		return {
			loading: false,
			data: {
				name: this.$store.state.mine.name,
				desc: this.$store.state.mine.desc
			}
		};
	},
	onLoad: function(option) {},
	methods: {
		submit() {
			if (!this.data.name) {
				uni.showToast({
					title: '名称不能为空~~',
					icon: 'none'
				});
				return false;
			}

			this.$store.commit('updateUserInfo', this.data);
			uni.showToast({
				title: '修改成功',
				icon: 'none'
			});
			setTimeout(() => {
				uni.hideToast();
				uni.navigateBack({
					delta: 1
				});
			}, 1000);
		}
	}
};
